// Subscription plugin.
import * as actions from '../interface/actions'
import * as eventTypes from '../interface/eventTypes'
import { DISCONNECT_REASONS } from '../constants'

// Auth plugin
import { getUserInfo } from '../../auth/interface/selectors'
import * as authEventTypes from '../../auth/interface/eventTypes'

/**
 * Operation factory function responsible for handling the 'connection lost' notification.
 * @method createOperation
 * @param  {Object} container The bottle container.
 * @return {Function} The 'handling of loss of connection' operation.
 */
export default function createOperation(container) {
  const { context, emitEvent, SubscriptionIntervals } = container

  async function handleLossOfConnection() {
    const userInfo = getUserInfo(context.getState())

    // Stop the automatic re-subscription interval.
    SubscriptionIntervals.resubInterval.stopResubInterval()

    context.dispatch(
      actions.unsubscribeFinished({
        reason: DISCONNECT_REASONS.LOST_CONNECTION
      })
    )

    // Dispatch an event with same payload, for backwards compatibility.
    emitEvent(userInfo.isAnonymous ? authEventTypes.AUTH_CHANGE : eventTypes.SUB_CHANGE, {
      reason: DISCONNECT_REASONS.LOST_CONNECTION
    })
  }

  return handleLossOfConnection
}
