// Operations.
import directTransferOperation from './directTransfer'
import validate from './validate'
import remoteSuccess from './remoteSuccess'
import remoteFailure from './remoteFailure'

import createFlow from '../../callManager/flows/DirectTransfer'

import { OPERATIONS } from '../../../interface/constants'

export default function initOperation(bottle) {
  // Provide the top-level container to the factory functions.
  //    Otherwise they would get the `operations` sub-container.
  bottle.factory('Callstack.operations.directTransfer', () => {
    const negFactory = bottle.container.Callstack.models.Negotiation

    /**
     * Factory function for a LocalDirectTransfer operation.
     * @method LocalDirectTransfer
     * @param {string} callId ID for the call this operation will affect.
     * @return {Operation} An instance of a local direct transfer operation.
     */
    function LocalDirectTransfer(callId) {
      return negFactory.instance(
        {
          // Operation meta-data.
          type: OPERATIONS.DIRECT_TRANSFER,
          isLocal: true,
          // Operation methods.
          stages: {
            validate,
            // TODO: This operation isn't a negotiation, so we shouldn't be naming
            //    a stage with WebRTC negotiation terms. But this is the method
            //    name the `localOperation` flow uses.
            localOffer: directTransferOperation(bottle.container),

            remoteSuccess: remoteSuccess(bottle.container),
            remoteFailure: remoteFailure(bottle.container)
          },
          createFlow
        },
        {
          callId
        }
      )
    }

    return {
      local: LocalDirectTransfer
    }
  })
}
